import { Component, h, Prop, Build, State, Element } from '@stencil/core';
import { account, AccountData } from "../../../global/data/account";
import { loadCommonData } from '../../../global/functions/loadCommon';

@Component({
    tag: 'ks-page-account',
    styleUrl: 'account.css'
})
export class PageAccount {
    @Element() root: HTMLElement;

    @Prop() skipbase: boolean;
    @Prop() commonData: string;
    @Prop() commonDynamicData: string;
    
    @Prop() accountData: string;

    @Prop({mutable: true, reflect: true}) menu: boolean;

    componentWillLoad() {
        const accountDataElement = document.getElementById(this.accountData);
		const accountData = JSON.parse(accountDataElement.innerHTML) as AccountData;

        Object.keys(accountData).map(key => {
			account.set(key as keyof AccountData, accountData[key]);
		});

        loadCommonData(this.commonData, this.commonDynamicData, Build);
    }

    @State() active = 0;

	render() {
        const strings = account.get("strings");
        const pages = account.get("pages");

		return <ks-page-base loadCommon={false} skipbase={this.skipbase} commonData={this.commonData} commonDynamicData={this.commonDynamicData}>
            <div class="account-page">
                <div class="heading">
                    <h1><span class="account-page-heading">{strings.header}: </span>{account.get("name")}</h1>
                    <button class="menu" onClick={()=>this.menu = !this.menu}>
                        <p class="pagename">{account.get("pages")[this.active].name}</p>
                        <ks-icon name="menu"/>
                    </button>
                </div>
                <div class="navigation">
                    {pages.map((page, index) => this.navigationElement(index, page.name))}
                </div>
                <div class="page">
                    {pages.map((page, index) => this.pageElement(index, this[page.key]))}
                </div>
            </div>

            <ks-account-order-modal />
            <ks-account-return-dialog />
        </ks-page-base>;
	}

    navigationElement(index: number, name: string) {
        return <div onClick={()=>this.navigationElementClick(index)} class={this.active == index ? "active" : null}>
            {name}
        </div>
    }

    navigationElementClick(index: number) {
        this.active = index;
        this.menu = false;
    }

    pageElement(index: number, page: ()=>any | (()=>any[])) {
        return <div class={this.active == index ? "pagecontent active" : "pagecontent"}>{page()}</div>;
    }

    empty() { return null }

    addresses() { return <ks-additional-adresses/> }
    products()  { return <ks-account-products /> }
    password()  { return <ks-password-form /> }
    returns()   { return <ks-account-returns /> }
    orders()    { return <ks-account-orders /> }
    data()      { return <ks-client-data-form /> }

    points() { return [
        <ks-points-overview />,
        <hr class="separator" />,
        <ks-points-history />
    ]}
}




