ks-page-account .account-page {
    display: grid;
    grid-template-columns: 230px 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas: 
        "header header"
        "navigation page";

    background-color: white;

    min-height: 600px;
    position: relative;
}

ks-page-account .account-page > .heading {
    grid-area: header;

    display: flex;
    justify-content: space-between;
    padding: 18px 20px;
    height: 65px;
    box-sizing: border-box;

    border-bottom: #ececec 1px solid;
}

ks-page-account .account-page > .heading > * {
    margin: 0;
}

ks-page-account .separator {
    margin: 20px -20px;
    border: none;
    border-top: 1px solid #ececec;
}

ks-page-account .account-page > .navigation {
    grid-area: navigation;

    border-right: #ececec 1px solid;
}

ks-page-account .account-page > .navigation > * {
    padding: 10px 20px;
    border-bottom: #ececec 1px solid;
    user-select: none;

    background: linear-gradient(to right, #eaeaea, #ffffff);
    background-repeat: no-repeat;
    background-position-x: -230px;

    transition: background-position-x 0.3s ease;
}

ks-page-account .account-page > .navigation > *:hover {
    background-position-x: -100px;
}

ks-page-account .account-page > .navigation .active {
    background-position-x: 0px;
}

ks-page-account .account-page > .page {
    grid-area: page;
    padding: 20px;
}

ks-page-account .account-page > .page > * {
    animation: fade-in 0.3s ease;
}


ks-page-account .pagecontent {
    display: none;
}

ks-page-account .pagecontent.active {
    display: block;
}

ks-page-account .menu {
    display: none;
    background-color: transparent;
    border: none;
}

ks-page-account .menu .pagename {
    margin: 0 10px 3px 0;
    font-size: 16px;
    font-family: var(--font-emphasis);
    color: #252525;
}

@media screen and (max-width: 960px) {
    ks-page-account .menu {
        display: flex;
        align-items: center;
    }

    ks-page-account .account-page {
        display: block;
    }

    ks-page-account .account-page-heading {
        display: none;
    }

    ks-page-account .account-page .page {
        opacity: 1;
        transition: opacity 0.3s ease,
    }

    ks-page-account[menu] .account-page .page {
        opacity: 0;
    }
    
    ks-page-account .navigation {
        position: absolute;
        top: 65px;
        bottom: 0px;
        right: -200px;

        width: 200px;
        
        z-index: 100;
        box-sizing: border-box;
        border-right: none;
        border-left: #ececec 1px solid;
        background-color: white;
        
        transition: transform 0.3s ease;
        transform: translateX(0px);
    }

    ks-page-account[menu] .navigation {
        transform: translateX(-200px);
    }
    
}